import L from 'leaflet';
import './style.css';
import config from '~/config';
import '~/lib/leaflet.control.commons';
import {fetch} from '~/lib/xhr-promise';
import {notify} from '~/lib/notifications';
import iconPointer from '../leaflet.control.azimuth/pointer.svg';

L.Control.Weather = L.Control.extend({
    options: {
        position: 'topleft',
    },

    onAdd: function (map) {
        this.places = null; // 
        this.data = null; //   
        this.periods = null; //  timestamp 

        this.icon_map = {
            '01d' : 'skc_d',
            '02d' : 'bkn_d',
            '03d' : 'ovc',
            '04d' : 'ovc',
            '09d' : 'ovc_ra',
            '10d' : 'bkn_ra_d',
            '11d' : 'ovc_+ra',
            '13d' : 'bkn_+sn_d',
            '50d' : 'ovc',
            '01n' : 'skc_n',
            '02n' : 'bkn_n',
            '03n' : 'ovc',
            '04n' : 'ovc',
            '09n' : 'ovc_ra',
            '10n' : 'bkn_ra_n',
            '11n' : 'ovc_+ra',
            '13n' : 'bkn_+sn_n',
            '50n' : 'ovc',
        }


        this.layerGroup = L.layerGroup();
        this.layerGroup.addTo( map );

        this._map = map;

        this.container = L.DomUtil.create('div', 'leaflet-control button-with-bar leaflet-control-weather');
        let link_container = L.DomUtil.create('div', 'leaflet-bar leaflet-control-single-button', this.container);
        this.link = L.DomUtil.create('a', 'leaflet-control-weather-button leaflet-bar-part ', link_container);
        this.link.href = '#';
        L.DomEvent.on(this.link, 'click', this.load, this);

        return this.container;
    },
    load: function(map) {
        if ( map._simulated ) {
            return this.stopPropagation(map);
        }
        if ( L.DomUtil.hasClass( this.link, 'leaflet-control-weather-loading' ) ) {
            return this.stopPropagation(map);
        }

        if ( L.DomUtil.hasClass( this.container, 'active' ) ) {
            this.unload();
            return this.stopPropagation(map);
        }
        L.DomUtil.addClass( this.link, 'leaflet-control-weather-loading' );
        const url = config.weatherServer;
        let self = this;
        if ( self.places ) {
            setTimeout( function() {
                self.showControl(map);
            }, 100 );
        } else {
            fetch(url, {method: 'GET', withCredentials: false}).then(function(response) {
                if ( response.status !== 200 ) {
                    notify( '     ' );
                    return false;
                }
                let res = JSON.parse( response.responseJSON );
                self.places = res.objects;
                self.data = res.data;
                self.periods = res.periods;

                //console.log(self.places, self.data, self.periods);
                self.showControl(map);
            });
        }

        return this.stopPropagation(map);
    },
    stopPropagation: function(map) {
        map.preventDefault();
        map.stopPropagation();
        return false;
    },
    renderMarkerIcon: function( place, current_weather ) {
        let temp = Math.round( current_weather.t );
        if ( temp > 0 ) {
            temp = '+' + temp;
        }
        let html = '<div class="balloon">\n' +
            '<a tabindex="0" href="#">\n' +
            '<img class="icon" src="https://yastatic.net/weather/i/icons/blueye/color/svg/'+self.icon_map[current_weather.i]+'.svg">' +
            '<span class="balloon__temp">'+temp+'</span>' +
            '<span class="balloon__name">'+place.name+'</span>' +
            '</a>\n' +
            '</div>\n';

        return html;
    },
    renderPlaces: function( period_index = 0 ) {
        self = this;
        const iconSingle = L.icon({iconUrl: iconPointer, iconSize: [30, 30]});
        for ( let place in self.places ) {
            let current_period = Object.keys(self.data)[period_index];
            let current_weather = self.data[current_period][place];
            if ( typeof current_weather === 'undefined' ) {
                continue;
            }
            let icon = L.divIcon({
                className: "marker-default",
                html: self.renderMarkerIcon( self.places[place], current_weather )
            });
            let marker = L.marker([self.places[place].lat, self.places[place].lng], {icon: icon});
            self.layerGroup.addLayer( marker );
        }
        document.querySelectorAll(".balloon").forEach(function(baloon ) {
            baloon.addEventListener("mouseenter", function(e) {
                baloon.querySelector(".balloon__name").style.display = 'inline-block';
                //baloon.querySelector(".baloon-content").style.display = 'block';
                let marker = baloon.closest('.marker-default');
                marker.dataset.zindex = marker.style['z-index'];
                marker.style['z-index'] = 99999;
                marker.style['min-width'] = '150px';
            });
            baloon.addEventListener("mouseleave", function(e) {
                baloon.querySelector(".balloon__name").style.display = 'none';
                //baloon.querySelector(".baloon-content").style.display = 'none';
                let marker = baloon.closest('.marker-default');
                marker.style['z-index'] = marker.dataset.zindex;
                marker.style['min-width'] = 'inherit';
            });
            baloon.addEventListener("click", function(e) {
                e.stopPropagation();
                e.preventDefault();
            });
        });

    },
    showControl: function(map) {
        L.DomUtil.removeClass( this.link, 'leaflet-control-weather-loading' );

        L.DomUtil.addClass(this.container, 'active');
        this._map.fire( 'weather_rendered' );

        this.renderPlaces();

        this.controlContainer = L.DomUtil.create('div', 'leaflet-bar bar leaflet-control-weather-bar', this.container);

        this.positionSliderLabel = L.DomUtil.create('label', 'leaflet-control-weather-label leaflet-bar-part', this.controlContainer);
        this.positionSliderLabel.for = "rainviewer-positionslider";
        this.positionSliderLabel.textContent = '';

        this.positionSlider = L.DomUtil.create('input', 'leaflet-control-weather-positionslider leaflet-bar-part', this.controlContainer);
        this.positionSlider.type = "range";
        this.positionSlider.id = "rainviewer-positionslider";
        this.positionSlider.min = 0;
        this.positionSlider.max = Object.keys(this.data).length-1;
        this.positionSlider.value = 0;
        L.DomEvent.on(this.positionSlider, 'input', function(e) {
            this.setCurrentPeriod( e.target.value );
        }, this);
        this.setCurrentPeriod( 0 );
        L.DomEvent.disableClickPropagation(this.positionSlider);

        this.closeButton = L.DomUtil.create('div', 'leaflet-control-weather-close', this.container);
        L.DomEvent.on(this.closeButton, 'click', this.unload, this);
    },
    unload: function(e) {
        L.DomUtil.remove(this.controlContainer);
        L.DomUtil.remove(this.closeButton);
        L.DomUtil.removeClass(this.container, 'active');
        this.layerGroup.clearLayers();
    },
    setCurrentPeriod( period_index = 0 ) {
        this.layerGroup.clearLayers();
        let current_period = Object.keys(this.data)[period_index];
        this.renderPlaces(period_index);
        this.positionSliderLabel.textContent = this.periods[current_period];
    },
    onRemove: function (map) {
        // Nothing to do here
    }
});